import { Button as ButtonComponent, ButtonSize, ButtonColor } from 'im.v2.component.elements';
import { BaseMessage } from 'im.v2.component.message.base';

import { tools } from './const/tools';
import './css/enable-functionality.css';

import type { ImModelMessage } from 'im.v2.model';
import type { CustomColorScheme } from 'im.v2.component.elements';

const TOOL_ID_PARAMS_KEY = 'toolId';
const BUTTON_COLOR = '#52c1e7';

// @vue/component
export const SupervisorEnableFeatureMessage = {
	name: 'SupervisorEnableFunctionality',
	components: { ButtonComponent, BaseMessage },
	props: {
		item: {
			type: Object,
			required: true,
		},
		dialogId: {
			type: String,
			required: true,
		},
	},
	data(): {showAddToChatPopup: boolean}
	{
		return {
			showAddToChatPopup: false,
		};
	},
	computed:
	{
		message(): ImModelMessage
		{
			return this.item;
		},
		ButtonSize: () => ButtonSize,
		ButtonColor: () => ButtonColor,
		buttonColorScheme(): CustomColorScheme
		{
			return {
				backgroundColor: 'transparent',
				borderColor: BUTTON_COLOR,
				iconColor: BUTTON_COLOR,
				textColor: BUTTON_COLOR,
				hoverColor: 'transparent',
			};
		},
		toolId(): string
		{
			return this.message.componentParams?.[TOOL_ID_PARAMS_KEY];
		},
		tool(): { title: string, description: string }
		{
			return tools[this.toolId];
		},
		imageModify(): string
		{
			return `--${this.toolId}`;
		},
	},
	methods:
	{
		loc(phraseCode: string, replacements: {[p: string]: string} = {}): string
		{
			return this.$Bitrix.Loc.getMessage(phraseCode, replacements);
		},
	},
	template: `
		<BaseMessage
			:dialogId="dialogId"
			:item="item"
			:withContextMenu="false"
			:withBackground="false"
			class="bx-im-message-enable-functionality__scope"
		>
			<div class="bx-im-message-enable-functionality__container">
				<div :class="['bx-im-message-enable-functionality__image', imageModify]" />
				<div class="bx-im-message-enable-functionality__content">
					<div class="bx-im-message-enable-functionality__title">
						{{ loc(tool.title) }}
					</div>
					<div class="bx-im-message-enable-functionality__description">
						{{ loc(tool.description) }}
					</div>
					<div class="bx-im-message-enable-functionality__buttons_container">
						<ButtonComponent
							:size="ButtonSize.L" 
							:isRounded="true"
							:text="loc('IM_MESSAGE_SUPERVISOR_ENABLE_FUNCTIONALITY_BUTTON_OPEN_SETTINGS')"
						/>
						<ButtonComponent
							:size="ButtonSize.L"
							:customColorScheme="buttonColorScheme"
							:isRounded="true"
							:text="loc('IM_MESSAGE_SUPERVISOR_ENABLE_FUNCTIONALITY_BUTTON_MORE_DETAILED')"
						/>
					</div>
				</div>
			</div>
		</BaseMessage>
	`,
};
